import Router from 'react';
import {Link} from 'react-router-dom';

const NavBar = () => (
    <nav>
        <ul>
            <li>
                <Link to="/">Home</Link>
            </li>
            <li>
                <Link to="/about">About me</Link>
            </li>
            <li>
                <Link to="/article">A-Page</Link>
            </li>
            <li>
                <Link to="/articles-list">A-List</Link>
            </li>
        </ul>
    </nav>
)

export default NavBar;